:root {
    --primary: #59ab07;
    --primary2: #4d8a0f;
    --default: white;
    --primary3: #80ff00;
}

.slider-super-cont {
    text-align: center;
    padding-inline: 8vw;
    background-color: rgb(23, 46, 26);
    min-width: 420px;
}
.slider-super-cont h2  {
    padding-top: 10px;
    color: var(--default);
    font-family: 'Digitone';
}

.slidercont {
    text-align: center;
}

.slide {
    max-width: 100%;
    margin: 0 auto;
    text-decoration: none;
    color: white;
    font-size: 30px;
    padding: 1rem;
    /* border: 1px solid white; */
}

.slide .slide-div {
    /* border: 1px solid white; */
    margin: 0 auto;
    max-width: 70%;
    padding: 20%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 14px;
}

.slick-prev:before {
    font-size: 2rem !important;
}

.slick-next:before {
    font-size: 2rem !important;
}

.slide p {
    /* margin: 10px; */
    padding-top: 10px;
    font-size: 1.5rem;
}

@media only screen and (max-width: 540px) {

    .slick-prev:before,
    .slick-next:before {
        font-size: 1rem !important;
    }

    .slide .slide-div {
        max-width: 100%;
        padding: 30%;
    }
}