:root {
  --primary: #59ab07;
  --primary2: #4d8a0f;
  --default: white;
  --primary3: #80ff00;
}

@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inknut+Antiqua&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

@font-face {
  font-family: 'Digitone';
  src: url('../font/DigitoneRegular.woff2') format('woff2'),
    url('../font/DigitoneRegular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

.aboutlink {
  background-color: rgba(0, 0, 0, 0.861);
  color: #ffffff;
  padding: 30px 20px 30px 50px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  /* border: 1px solid greenyellow; */
  min-width: 420px;
}

.check {
  padding: 50px;
  margin-top: 50px;
  letter-spacing: 1px;
}

.aboutlink div .type {
  font-size: 4rem;
  font-weight: bolder;
  
}

.Typewriter__wrapper{
  font-family: 'Digitone'!important;
  padding-left: 10px;
  /* color: black; */
  border-bottom: 1px solid;
  /* color: #008080;
  text-shadow: 1px 1px #ffffff; */
}

.aboutlink h1 {
  display: block;
  font-size: 4rem;
  margin-left: 100px;
}

.aboutlink .alink {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

#agritext {
  font-family: 'Digitone';
  font-weight: bold;
  /* color :black; */
  border-bottom:1px solid;
  /* color: #008080;
  text-shadow: 1px 1px #ffffff; */
}

.aboutlink .alink div {
  display: flex;
  width: 50%;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.aboutlink .alink div h1 {
  margin: -80px 42px 30px 28px;
  padding: 10px 50px;
}

.aboutlink .alink .about--us {
  /* border: 1px solid ; */
  font-size: 1.2rem;
  margin-top: 0;
  padding: 0 10px!important;
  margin-bottom: 1rem;
  font-weight: 350;
  font-family: 'Poppins', sans-serif;
  text-align: justify;
  background-color: Antiqua;

  border: 7px solid green; 
  box-shadow: 7px 7px 10px 0px white;
  border-radius: 30px ;
}

.about--us{
  /* justify-content: space-evenly; */
  /* word-break: break-all; */
  /* padding: 30px 10%; */
  word-wrap: break-all;
  word-break: keep-all;
  /* word-spacing: 2px;  */
}

