.modal-content{
  min-width: 560px;
  margin-top: 60px;
  /* background-color: var(--primary2); */
  background-color: purple;
  color: var(--default);
}

.about--us--btn{
  width: auto;
  padding: 0px 20px;
  height: 50px;
  background-color: var(--primary) !important;
  color: var(--default) !important;
  font-size: 1.1rem;
  font-weight: 600;
  margin: 10px;
  border: none;
  outline: none;

  border-radius: 12px;
  border:2px double;
}


.aboutlink .alink .about--us--btn:hover {
  /* border: 2px solid #035500; */
  background-color:  rgba(0, 255, 13, 0.64);
  /* color: #035500; */
  text-shadow: 0 0 10px #fff;
}

.about--us--btn :active{
  transform: translateY(4px); 
}

.at h1 {
  font-family: 'Poppins', sans-serif;
}

.about--us {
  padding-left: 50px !important;
}

.about--us--text{
  display: grid;
  grid-template-columns: repeat(3, 1fr) ;
  grid-gap: 10px;
  padding: 50px;
}



@media only screen and (max-width: 1201px) {
  #tree {
    margin-left: 140px;
    /* border: 1px solid brown; */
  }

  .aboutlink>div {
    width: 100%;
  }

  .aboutlink div {
    display: inline-block;
    /* border: 1px solid rebeccapurple; */
  }

  .aboutlink div .type {
    display: inline-block;
  }

  #agritext {
    margin-left: 10px;
  }
  .aboutlink .alink div .us {
    margin-top: -57px;
    margin-left: 70%;
  }
}

@media only screen and (max-width: 953px) {
  .aboutlink .alink div {
    width: 80%;
  }

  #tree {
    width: 450px;
    margin-left: 0px;
  }
}

@media only screen and (max-width: 883px) {
  /* .aboutlink {
    padding: 5px 0px 0 100px;
  } */

  .aboutlink div .type,
  .aboutlink h1 {
    font-size: 3rem;
  }

  .aboutlink .alink h1 {
    font-size: 4rem;
  }
}

@media only screen and (max-width: 633px) {
  .aboutlink {
    padding: 0 0px 0 80px;
  }

  .aboutlink div .type,
  .aboutlink h1 {
    font-size: 2rem;
  }

  .aboutlink .alink h1 {
    font-size: 4rem;
  }
  .aboutlink .alink div .us {
    margin-left: 80%;
  }
}

@media only screen and (max-width: 596px) {
  .aboutlink .alink div .us {
    margin-left: 90%;
  }
}

@media only screen and (max-width: 553px) {
  .aboutlink {
    padding: 0 0px 20px 10px;
  }

  .aboutlink .alink div {
    width: 100%;
  }

  #tree {
    width: 300px;
    margin-left: 50px;
    padding: 0;
  }

  .aboutlink .alink div h1 {
    padding: 10px 0px 0 50px;
  }
}

@media only screen and (max-width: 422px) {
  /* .aboutlink .alink div  { */
  .aboutlink .alink .about--us  {
    display:grid;
    justify-content: stretch;
    text-align: center left ;
    margin: 0px 10px 0px 4px;
    
    
    /* margin-left: 80%;
    margin-top: -50px; */
  }

  .aboutlink div .type,
  .aboutlink h1 {
    font-size: 1.8rem;
  }

  .aboutlink .alink h1 {
    font-size: 4rem;
  }
  .aboutlink .alink span {
    margin: 0 auto;
  }
}