@font-face {
    font-family: 'Digitone';
    src: url('../font/DigitoneRegular.woff2') format('woff2'),
        url('../font/DigitoneRegular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

.roadmap {
    width: 100%;
    height: 100%;
    min-width: 420px;
    overflow:hidden;
    scroll-behavior: smooth;
    background: url("https://raw.githubusercontent.com/AGRIVERSEdevelopers/resources/New/BG-roadmap.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: 30px 30px ; 
}

.underline{
    border-bottom: 1px solid white;
    margin-bottom: 20px;
}

.roadmap  h3,
.roadmap  h1{
    margin: 0 auto;
    color: #fff;
    width: max-content;
    font-family: 'Poppins', sans-serif;
}
.roadmap  h3{
    top: 10%;
    left: 32%;
    font-family: 'Open Sans', sans-serif;
}

.roadmap  h1{
    top: 15%;
    left: 42% ;
    font-family: 'Digitone';
    font-weight: bold;
    font-size: 350%;
}

.roadmap .roadmap-nav {
    /* border: 1px solid red; */
    width: 400px;
    min-width: max-content;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    position: sticky;
    z-index: 9990;
    margin-left: 20px;

}

.roadmap .roadmap-nav a {
    /* border: 1px solid yellow; */
    padding: 10px;
    font-size: x-large;
    font-weight: bolder;
    color: rgb(99, 165, 0);
    text-decoration: none;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
}

.roadmap .roadmap-nav a:hover {
    color: #fff;
}

.roadmap .btn-div{
    display: flex;
    flex-direction: row !important;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    height: 80vh;
}

.roadmap .wrapper {
    width: 100%;
    height: 83%;
    display: flex;
    overflow-y: hidden;
    margin: 0 auto;
    scroll-behavior: smooth;
    align-items: baseline;
    scroll-snap-type: x mandatory;
}

.roadmap .wrapper::-webkit-scrollbar {
    width: 0px;
    height: 0px;
}

.roadmap .wrapper .wrap-item {
    min-width: 100%;
    height: 100%;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    scroll-snap-align: center;
    scroll-snap-stop: always;
    scroll-snap-destination: center;
}

.roadmap .wrapper .wrap-item .div-boxing {
    width: 100%;
    height: 100%;
    /* background-color: rgba(255, 255, 255, 0.582); */
    display: flex;
    justify-content: space-evenly;
    align-items: flex-end;
}

.roadmap .wrapper .wrap-item .div-boxing .card-boxses{
    /* border: 1px solid rebeccapurple; */
    min-width: 200px;
    min-height: 200px;
    width: min-content;
    height: max-content;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    transition: all 0.3s ease;
    border-radius: 14px;
    padding: 0 0 10px;
    cursor: pointer;
    z-index: 9;
    width: min-content;
    background-color: transparent;
    color: transparent;
    flex-direction: column-reverse;
}

.roadmap .wrapper .wrap-item .div-boxing .card-boxses:nth-child(n){
    width: max-content;
    background-color: rgba(0, 0, 0, 0.274);
    color: #fff;
    flex-direction: row;
}

.roadmap .wrapper .wrap-item .div-boxing .card-boxses:hover{
    width: max-content;
    background-color: rgba(0, 0, 0, 0.274);
    color: #fff;
    flex-direction: row;
    transform: scale(1.1);
}


.roadmap .wrapper .wrap-item .div-boxing .card-boxses span{
    min-width: 15px;
    min-height: 15px;
    border-radius: 100%;
    cursor: pointer;
    background-color: #fff;
    z-index: 99;
}
/* bottom: 20px; */
.roadmap .wrapper .wrap-item .div-boxing .card-boxses div{
    /* border: 1px solid red; */
    width: 100%;
    height: 100%;
    border-radius: 14px;
    font-size: small;
    background-color: inherit;
    color: inherit;
    position: relative;
    /* bottom: 20px; */
}


.roadmap .wrapper .wrap-item .div-boxing .card-boxses div h2 {
    width: max-content;
    position: relative;
    left: 10px;
    padding: 10px;
    font-family: 'Poppins', sans-serif;

}

.roadmap .wrapper .wrap-item .div-boxing .card-boxses ul {
    /* background-color: #fff; */
    position: relative;
    bottom: 15px;
    padding: 0px 30px;
    text-align: left;
}

.roadmap .wrapper .wrap-item .div-boxing .card-boxses ul li{
    font-family: 'Open Sans', sans-serif;
    letter-spacing: 0.7px;
    line-height: 30px;
}

.roadmap .wrapper .wrap-item .div-boxing .card-boxses .hidden li{
    opacity: 0;
}


.roadmap .wrapper .wrap-item .div-boxing .card-boxses .hidden li:nth-child(1){
    opacity: 1;
}

.roadmap .scroll-btn {
    padding: 4px 14px;
    font-size: larger;
    border: none;
    border-radius: 50%;
    background-color: rgb(0, 160, 0);
    color: #fff;
    margin: 0 60px;
    cursor: pointer;
    z-index: 99;
}

#right-btning {
    left:90%;
}

#left-btning {
    left: 3%;
}

#scale{
    width: 100%;
    height: 6px;
    border-radius: 80px;
    background-color: rgb(0, 255, 13);
    margin-top: -50px;
    /* z-index: 5000; */
}

